/*
        ========================================
        CẤU HÌNH CHUNG & HIỆU ỨNG 3D
        ========================================
        */
body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    margin: 0;
    background-color: #f0f0f0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    overflow: hidden;
    /* Ngăn cuộn trang khi có hiệu ứng */
}

.card-container {
    /* Thiết lập góc nhìn 3D (perspective) cho hiệu ứng trồi lên */
    perspective: 1500px;
    position: relative;
}

/*
        ========================================
        PHONG BÌ (ENVELOPE) & PHONG ẤN (SEAL)
        ========================================
        */
.envelope {
    position: absolute;
    width: 300px;
    height: 200px;
    background-color: #8B4513;
    /* Màu nâu đất */
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    /* Thêm hiệu ứng chuyển động mượt mà cho phong bì */
    transition: all 1.5s ease-in-out;
    transform-style: preserve-3d;
    z-index: 20;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    /* Căn giữa tuyệt đối trong container */
    transform: translate(-50%, -50%) rotateY(0deg);
    top: 50%;
    left: 50%;
}

.seal-button {
    background-color: #e5cc8a;
    /* Màu vàng đồng (giả lập sáp) */
    color: #8B4513;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    cursor: pointer;
    border: 4px solid #b89c67;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3), inset 0 0 10px rgba(255, 255, 255, 0.5);
    font-size: 14px;
    -webkit-user-select: none;
    user-select: none;
    transition: transform 0.2s;
    text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.4);
}

.seal-button:hover {
    transform: scale(1.05);
}

.seal-button:active {
    transform: scale(0.95);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5), inset 0 0 5px rgba(255, 255, 255, 0.5);
}

/*
        ========================================
        THIỆP (CARD)
        ========================================
        */
.absolute-center {
    position: absolute;
    top: 50%;
    left: 50%;
}

.card {
    position: absolute;
    width: 500px;
    height: fit-content;
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3);
    text-align: center;
    color: #333;
    padding: 20px;
    box-sizing: border-box;

    /* Thẻ thiệp ban đầu nằm phẳng và ẩn bên trong phong bì */
    opacity: 0;
    transform-origin: center center;
    transform: translate(-50%, -50%) rotateX(90deg) translateZ(-100px);

    /* Căn giữa tuyệt đối trong container */
    top: 50%;
    left: 50%;

    /* Thêm hiệu ứng chuyển động mượt mà cho thiệp */
    transition: transform 1.5s cubic-bezier(0.68, -0.55, 0.27, 1.55), opacity 1s ease-in;
    z-index: 10;
}

/*
        ========================================
        TRẠNG THÁI MỞ (OPEN STATE)
        ========================================
        */

/* Hiệu ứng: Phong bì lùi lại và mờ đi */
.card-container.open .envelope {
    transform: translate(-50%, -50%) rotateY(10deg) translateZ(-250px);
    opacity: 0;
    pointer-events: none;
    /* Quan trọng: Vô hiệu hóa nút sau khi mở */
}

/* Hiệu ứng: Thiệp trồi lên, xoay về vị trí thẳng đứng và phóng to nhẹ */
.card-container.open .card {
    opacity: 1;
    /* trồi lên (translateZ), xoay về (rotateX), và phóng to nhẹ (scale) */
    transform: translate(-50%, -50%) rotateX(0deg) translateZ(100px) scale(1.1);
    box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
    /* Tạo bóng rõ hơn khi trồi lên */
}

/* Nội dung thiệp */
.card h2 {
    color: #a00000;
    border-bottom: 2px solid #a00000;
    padding-bottom: 5px;
    margin-top: 0;
}

.card p {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 10px;
}